
import { defineComponent } from 'vue'
import { COM21500311_REQ_BODY, COM21500311_RES, ScreenInfo } from '@/shared/type/COM21500311.type'
import { COM21100113_RES } from '@/shared/type/APM20100131.type'
import { COMMON_KEY_VALUE, PAGE_SIZE } from '@/shared/constants/common.const'
import Pagination from '@/shared/components/Pagination/Pagination.vue'
import BreadCrumb from '@/shared/components/BreadCrumb/BreadCrumb.vue';
import SelectOption from '@/shared/components/SelectOption/SelectOption.vue';

export default defineComponent({
    name: "com215003sc00Vue",
    components: {
        BreadCrumb,
        Pagination,
        SelectOption
    },
    data() {
        const header = [
            { columnName: "No" },
            { columnName: "Screen ID" },
            { columnName: "Screen Name" },
            { columnName: "Screen Type" },
            { columnName: "Business Category" },
            { columnName: "Status" }
        ];
        return {
            breadcrumbs: [
                { value: 'com215003sc00.ib_management', keyPath: '', type: 'label' },
                { value: 'com215003sc00.ib_screen_list', keyPath: '/COM215003SC00', type: 'route' },
            ],
            COMMON_KEY_VALUE,
            header,
            baseScreenList: [] as ScreenInfo[],
            screenList: [] as ScreenInfo[],
            searchData: {
                searchValue: "",
                screenName: "",
                screenID: "",
                businessCategoryCode: "",
                screenStatusCode: ""
            },
            totalItems: 0,
            currentPage: 1,
            pageSize: PAGE_SIZE,
            startingIndex: 1,
            businessCategoryItems: [] as any,
            selectedOption: '' as any,
            isResetPage: false
        }
    },
    mounted() {
        this.onGetScreenList();
    },
    methods: {
        async onGetScreenList() {
            const reqBody: COM21500311_REQ_BODY = {
                pageNumber: this.currentPage,
                pageSize: this.pageSize,
                businessCategoryCode: this.searchData.businessCategoryCode,
                searchValue: this.searchData.searchValue.trim(),
                screenStatusCode: this.searchData.screenStatusCode
            }

            const responseData = await this.$request.getRequest('COM21500311', reqBody, false) as COM21500311_RES
            if (responseData.header.result) {
                const lists = responseData.body.list;
                const addedListKey: any = await this.onMappingTableData(lists);
                this.baseScreenList = addedListKey
                this.screenList = addedListKey;
                this.totalItems = responseData.body.totalCount
            }
        },
        async onMappingTableData(lists) {
            await this.$store.dispatch('CommonCodeModule/requestCommonCode');
            const commonCode = this.$store.state.CommonCodeModule.commonCode;
            const { BusinessCategoryCode, ScreenStatusCode, ScreenTypeCode } = this.$store.state.CommonCodeModule.commonCode;
            if (commonCode) {
                return lists.map((item, index) => ({
                    No: index + 1,
                    screenID: item.screenID,
                    screenName: item.screenName,
                    screenTypeCode: ScreenTypeCode && item.screenTypeCode ? `${ScreenTypeCode.find((type) => type.key === item.screenTypeCode)?.key}:${ScreenTypeCode.find((type) => type.key === item.screenTypeCode)?.value}` : '',
                    businessCategoryCode: BusinessCategoryCode && item.businessCategoryCode ? `${BusinessCategoryCode.find((category) => category.key === item.businessCategoryCode)?.key}:${BusinessCategoryCode.find((category) => category.key === item.businessCategoryCode)?.value}` : '',
                    screenStatusCode: ScreenStatusCode && item.screenStatusCode ? `${ScreenStatusCode.find((status) => status.key === item.screenStatusCode)?.key}:${ScreenStatusCode.find((status) => status.key === item.screenStatusCode)?.value}` : '',
                }));
            }

        },
        onClickRefresh() {
            this.currentPage = 1;
            this.pageSize = PAGE_SIZE;
            this.isResetPage = true;
            this.searchData = {
                searchValue: "",
                screenName: "",
                screenID: "",
                businessCategoryCode: "",
                screenStatusCode: ""
            }
            this.onGetScreenList();
        },
        async onClickSearch() {
            if (this.searchData.searchValue == '' && this.searchData.screenStatusCode == '' && this.searchData.businessCategoryCode == '') {
                this.isResetPage = true;
                this.currentPage = 1;
                this.pageSize = PAGE_SIZE;
                await this.onGetScreenList();
            } else {
                this.currentPage = 1;
                this.isResetPage = true
                await this.onGetScreenList();
            }
        },
        handleRegister() {
            this.$router.push({
                path: '/COM215003SC01',
                query: {
                    type: 'register'
                }
            })
        },
        handleRowClick(rowData) {
            if (!rowData) return
            this.$router.push({
                path: '/COM215003SC01',
                query: {
                    type: 'edit',
                    screenID: rowData
                }
            })
        },
        handlePagination(pageNumber: number) {
            this.currentPage = pageNumber;
            this.isResetPage = false;
            this.onGetScreenList();
        },

        handleStartingIndex(startingIndex: number) {
            this.startingIndex = startingIndex
        },

        async getBussinessCatgoryCode() {
            const responseData = await this.$request.getRequest('COM21100113', { businessCommonStatusCode: "01", languageCode: "", standardCodeDomainName: "BusinessCategoryCode", standardCodeValue: "" }, false) as COM21100113_RES
            this.businessCategoryItems = responseData.body.list;
        }
    }
})
